@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400&display=swap);
@import url ('https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css');
@import ulr ('https://cdn.jsdelivr.net/gh/devicons/devicon@v2.11.0/devicon.min.css');
.about-me-section-container{
    width: 100vw;
    min-height: 100vh;
    background-color: rgba(247,172,130,1) ;
}
.personal-photo{
    width: 500px;
}
.about-me-container{
    padding-top:80px;
    max-width: 1040px;
    margin: auto;
    display: flex;
    justify-content: space-around; 
    align-items: center;
}
.about-me-text{
    width: 80%;
    margin: 20px;
}
.about-me-container h2{
    font-size: 50px;
}
.about-me-container p {
    font-size: 20px;
}
.personal-photo-container{
    border-radius: 50%;
    overflow: hidden;
    min-width: 400px;
    max-width: 400px;
    height: 400px;
box-shadow: -2px 2px 12px -1px rgba(61,43,61,1);
}
.skills-title{
    font-size: 40px;
}
.skills-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.skill-logo-container{
    width: 80px;
    margin: 30px;
    
}
/*NO FUNCIONA :(*/
.skill-logo-container :hover{
    animation: bounce;
    animation-duration: 2s;
}
@media (max-width:769px){
    .about-me-container{
        flex-wrap: wrap;
    }
}
@media (max-width:481px){
    .personal-photo-container{
        width: 300px;
        height: 300px;
    }
    .personal-photo{
        width: 400px;
    }
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Gamas de colores de paletton:
Violetas: #400340 #571157-#864086 #9D619D #6F256F
Azulado: #226764 #033C3A #10514F #3B7D7B #5B9391
Tierra: F2D495
*/
.App {
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.art-dev-container{
    /* background: rgb(87,17,87);
    background: linear-gradient(193deg, rgba(87,17,87,1) 0%, rgba(16,81,79,1) 100%); */
    background: rgb(103,61,148);
    background: linear-gradient(29deg, rgba(103,61,148,1) 0%, rgba(247,172,130,1) 100%);
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.art-dev-avatar{
    width: 500px;
}
.button {
    width: 60px;
    text-align: center;
    background: none;
    border-radius: 10px;
    padding: 10px;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    border: 1px solid #fff;
    margin: 10px;
}
button a {
    text-decoration: none;
    color: #fff;
}
@media(max-width:520px){
    .art-dev-avatar{
        width: 90%;
    }
}
.main-container{
    /* background-color: #a186be; */
    background: rgb(103,61,148);
    background: linear-gradient(29deg, rgba(103,61,148,1) 0%, rgba(247,172,130,1) 100%);
    /* background: rgb(87,17,87);
    background: linear-gradient(193deg, rgba(87,17,87,1) 0%, rgba(16,81,79,1) 100%); */
    display: flex;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color:#fff;
}
.avatar-description-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.avatar-image{
    width: 500px;   
}
.avatar-container{
    flex-grow: 1;
}
.personal-description{
    flex-grow: 2;
}
.personal-description h1{
    font-size: 50px;
}
.personal-description span{
    font-size: 30px;
}
@media(max-width:520px){
    .avatar-image{
        width: 90%;
    }
    .personal-description h1{
        font-size: 40px;
    }
    .personal-description span{
        font-size: 25px;
    }
}
.navbar{
    position: fixed;
    top:-50px;
    height: 50px;
    width: 100vw;
    transition: top 0.3s;
    display: flex;
    justify-content: center;
    margin:auto;
    align-items: center;
}
.navbar-container{
 
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
.navbar-container{
    margin: 0;
    padding: 0;
}
.navbar-container li{
    margin: 0 10px;
    list-style: none;
    font-size: 20px;
    cursor: pointer;
}
.navbar-container li a {
    text-decoration: none;
    color: #fff;
}
.active{
    background-color:#fff;
    color:blueviolet;
    top:0;
}
